<template>
    <div class='a-box'>
        <Row class='a-row'>
            <i-col style='margin-top:10px;height:auto;background:white' :xs="{span:24}" :sm="{span:15}" :lg="{span:15}">
                <div class='about-me'></div>
                <div class='about-me-content'>
                    <span class='span'></span>
                    <span class='span'>2016,,</span>
                    <span class='span'>~~</span>
                </div>
            </i-col>

            <i-col style='margin-top:10px;background:white;' :xs="{span:24}" :sm="{span:15}" :lg="{span:15}">
               <div class='about-me'></div>
               <div class='about-me-content'>
                   <p><Icon size='16' type="ios-call-outline" /><span style='margin-left:5px'>13454143096</span></p>
                   <p><Icon size='16' type="ios-mail-outline" /><span style='margin-left:5px'>1026248690@qq.com</span></p>
                   <p><Icon size='16' type="logo-github" /> <span @click='gohub' class='about-hover' style='margin-left:7px;'>xuzhehui</span></p>
               </div>
            </i-col> 

            <i-col class-name='brief' :xs="{span:0}" :sm="{span:8,push:16}">
                <RigthView/>
            </i-col>  
             <i-col class-name='mysorch' :xs="{span:24}" :sm="{span:0}" :lg="{span:0}">
                <RigthView/>
            </i-col>  
        </Row> 
    </div>
</template>

<script>
import RigthView from '../plugins/rightView'
export default {
    data(){
        return {

        }
    },
    methods:{
        gohub(){
            window.open('https://github.com/xuzhehui')
        }
    },
    components:{
        RigthView
    }
}
</script>

<style lang="stylus" scoped>
    .brief
        margin-top 10px
        position absolute
        border-radius 10px
    .mysorch
        margin-top 10px
        border-radius 10px
    .a-box
        width 100%
        height 100%
        display flex
        justify-content center
        .a-row
            width 95%
            height auto
            display flex
            flex-direction column
        .about-me
            width 100%
            height 40px
            display flex
            justify-content center
            align-items center
            border-bottom 1px solid #e8eaec
            font-size 16px
        .about-me-content
            width 100%
            padding 10px
            display flex
            flex-direction column
            justify-content space-between
            font-size 14px
            .span
                margin-top 20px
            p
                height 40px
                display flex
                align-items center
                .about-hover
                    cursor pointer
                    &:hover
                        color rgb(45,183,245)

            

</style>





